<template>
	<view>
		<MNavbar title="运动" titleStyle="color:#fff;" :autoBack="false" bgColor="#F6CE46">
			<view slot="left"></view>
		</MNavbar>
		<view class="banner_box">
			<u-image :src="$formatImg(shopInfo.banner?shopInfo.banner[0]:null)" width="100%" height="370rpx"></u-image>
		</view>
		<u-notice-bar text="优惠啦"></u-notice-bar>
		<view class="card_box">
			<view class="left" @tap="goPath('/pages/shop/shop?id='+ prods[0].prodId)">
				<view>
					<u-image src="/static/index/img4.png" width="100rpx" height="auto" mode="widthFix"></u-image>
					<!-- <view>{{ prods[0].prodName || '次卡' }}</view> -->
					<view class="price_row">
						<text style="font-size: 22rpx;">￥</text>
						{{ prods[0].price || 24.9 }}
					</view>
				</view>
				<u-image src="/static/index/img6.png" mode="widthFix" height="auto" width="120rpx"></u-image>
			</view>
			<view class="right" @tap="goPath('/pages/shop/shop?id='+ prods[1].prodId)">
				<view>
					<u-image src="/static/index/img5.png" width="100rpx" height="auto" mode="widthFix"></u-image>
					<view class="price_row">
						<text style="font-size: 22rpx;">￥</text>
						{{ prods[1].price || 199 }}
					</view>
				</view>
				<u-image src="/static/index/img8.png" mode="widthFix" height="auto" width="120rpx"></u-image>
			</view>
		</view>
		<view class="info_box">
			<view class="info_title">
				<view class="logo">
					<u-image :src="$formatImg(shopInfo.logo)" radius="75rpx" width="90rpx" height="90rpx"></u-image>
				</view>
				<view>{{ shopInfo.shopName || '' }}</view>
			</view>
			<view class="addr_box">
				<view class="left">
					<view>{{ shopInfo.address || '' }}</view>
					<view>
						<u-image src="/static/index/time.png" width="25rpx" height="25rpx"></u-image>
						<view style="margin-left: 5rpx;">
							营业时间 {{ shopInfo.openingHours || ''}}
						</view>
					</view>
				</view>
				<view class="right">
					<view>
						<u-image src="/static/index/location.png" width="35rpx" height="35rpx"></u-image>
					</view>
					<view>
						<u-image src="/static/index/phone.png" width="35rpx" height="35rpx" @tap="call()"></u-image>
					</view>

				</view>
				<!-- <image src="/static/index/banner.png" class="bg_img" style="left: 0;bottom: 0;height: 50%;"></image> -->
			</view>
		</view>

	</view>
</template>

<script>
	import MNavbar from '@/components/MNavbar/MNavbar.vue'
	import {
		getProdsApi
	} from '@/apis/prod.js'
	import {
		getShopInfoApi
	} from '@/apis/shop.js'
	export default {
		data() {
			return {
				prods: [], // 商品列表
				shopInfo: {} // 店铺信息
			}
		},
		components: {
			MNavbar
		},
		onLoad() {
			this.getProds()
			this.getShopInfo()
		},
		methods: {
			// 获取商品
			getProds() {
				getProdsApi().then((res) => {
					this.prods = res
					console.log(res)
				}).catch(e => {
					console.error(e)
				})
			},
			// 获取店铺基础信息
			getShopInfo() {
				getShopInfoApi().then((res) => {
					if (res.banner) {
						res.banner = JSON.parse(res.banner)
					}
					this.shopInfo = res
					console.log(res)
				}).catch(e => {
					console.error(e)
				})
			},
			// 拨打电话
			call() {
				uni.makePhoneCall({
					phoneNumber: this.shopInfo.telephone,
					fail: () => {
						uni.showToast({
							title: "拨打电话失败",
							icon: 'error'
						})
					}
				})
			},
			goPath(url) {
				uni.navigateTo({
					url
				})
			}
		}
	}
</script>
<style>
	page {
		background: #F3F3F3;
	}
</style>
<style lang="scss" scoped>
	.banner_box {
		width: 750rpx;
		margin: 0 auto;
	}

	.info_box {
		position: relative;
		z-index: 9999;
		width: 690rpx;
		margin: 20rpx auto 0;
		box-sizing: border-box;
		// padding: 0 20rpx;
		box-shadow: #DDD 0 0 20rpx;
		background: #F5F5F5;
		border-radius: 15rpx;
		min-height: 150rpx;

		.info_title {
			box-sizing: border-box;
			padding: 20rpx 50rpx 0;
			display: flex;
			align-items: center;
			font-size: 32rpx;
			font-weight: 600;

			.logo {
				// margin-top: -35rpx;
				margin-right: 20rpx;
			}

			&>view:last-child {
				// margin-top: 10rpx;
			}
		}

		.addr_box {
			margin-top: 20rpx;
			display: flex;
			align-items: center;
			justify-content: space-between;
			padding: 0 30rpx 40rpx;

			.left {

				&>view:first-child {
					font-size: 28rpx;
					font-weight: 400;

				}

				&>view:last-child {
					padding: 15rpx 0 0;
					font-size: 22rpx;
					color: #666;
					display: flex;
					align-items: center;
				}

			}

			.right {
				height: 100%;
				display: flex;
				align-items: center;

				&>view {
					width: 56rpx;
					height: 56rpx;
					display: flex;
					justify-content: center;
					align-items: center;

				}

				&>view:first-child {
					background: #E1FDEF;
					border-radius: 50rpx;
					margin-right: 35rpx;
				}

				&>view:last-child {
					background: #D8F9FE;
					border-radius: 50rpx;
				}
			}
		}
	}

	.card_box {
		width: 690rpx;
		margin: 20rpx auto 0;
		display: flex;
		justify-content: space-between;

		.left,
		.right {
			width: 330rpx;
			height: 200rpx;
			color: #333;
			border-radius: 15rpx;
			overflow: hidden;
			display: flex;
			align-items: center;
			box-shadow: #DDD 0 0 20rpx;

			.bg_img {
				width: 100%;
				height: 80%;
				opacity: 0.8;
			}

			&>view:first-child {
				font-weight: 700;
				font-size: 35rpx;
				margin: 0 20rpx 0 50rpx;

				.price_row {
					font-size: 28rpx;
					color: #DF6B2E;
				}

			}
		}

		.left {
			// background: #F5FCEC;
			// border-radius: 70rpx 0 70rpx 0;
			// color:#9DE650
		}
	}
</style>